.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Calc {
  border: 2px solid grey;
  border-radius: 10px;
  padding: 10px;
  background-color: darkgrey;
}

.CalcModel {
  font-size: 0.65rem;
  padding-bottom: 4px;
}

.CalcGrid {
  display: inline-grid;
  grid-template-columns: auto auto auto auto;
  grid-template-rows: auto auto auto auto auto auto;
  grid-template-areas:
    'sc sc sc sc'
    'ac pm del div'
    'sev eig nin mul'
    'fou fiv six sub'
    'one two thr add'
    'zer zer dec eq';
  grid-gap: 12px 12px;
}

.CalcScreen {
  grid-area: sc;
  background-color: lightgreen;
  border: 2px solid grey;
  border-radius: 5px;
  padding: 10px 5px;
  text-align: right;
  min-width: 210px;
}

.CalcKey {
  border: 1px solid grey;
  border-radius: 5px;
  min-height: 45px;
  min-width: 52px;
  background-color: rgb(221, 221, 221);
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

